.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: var(--bodyBg);
  color: var(--textColor);
  font-family: var(--sfpro), "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 11px;
  line-height: 100%;
  letter-spacing: 0.02em;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  line-height: 130%;
  color: var(--accent);
  letter-spacing: 0.02em;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}

.container {
  width: 100%;
  padding: 0 14px;
  margin: 0 auto;
}

@media all and (min-width: 480px) {
  .container {
    padding: 0 18px;
  }
}
@media all and (min-width: 768px) {
  .container {
    padding: 0 30px;
    max-width: 1120px;
  }
}
@media all and (min-width: 1300px) {
  .container {
    max-width: 1276px;
  }
}
.btn {
  min-height: 40px;
  padding: 11px 16px 12px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  color: #fff;
  background: var(--btnColor);
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
}

.main {
  flex-grow: 1;
}

.header__top {
  background: #fff;
}
.header__top-inner {
  min-height: 50px;
  padding: 13px 4px;
  display: flex;
  align-items: center;
}
.header__burger {
  width: 24px;
  min-width: 24px;
  height: 24px;
  margin-right: 10px;
}
.header__city {
  display: none;
  color: var(--accent);
}
.header__city-current {
  display: inline-block;
  padding-right: 20px;
  position: relative;
  text-align: left;
  font-size: 11px;
  letter-spacing: 0.02em;
}
.header__city-arrow {
  width: 14px;
  height: 14px;
  fill: #33A2EB;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.header__time {
  display: none;
}
.header__contact {
  margin-left: auto;
  color: var(--accent);
  font-weight: 600;
  font-size: 11px;
  line-height: 100%;
}
.header__main {
  background: var(--accent);
  position: relative;
  color: #fff;
}
.header__main-inner {
  min-height: 254px;
  padding: 32px 14px 32px 20px;
}
.header__main-box {
  position: relative;
  z-index: 5;
}
.header__menu {
  margin-bottom: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: inline-block;
  width: 147px;
  min-width: 147px;
}
.header__logo img {
  width: 100%;
}
.header__nav {
  display: none;
}
.header__nav-link {
  position: relative;
}
.header__nav-link::after {
  content: "";
  width: 0;
  position: absolute;
  bottom: 0;
  right: 0;
  height: 1px;
  background: #fff;
  transition: width 0.3s ease;
}
.header__nav-link:hover::after, .header__nav-link:focus::after {
  width: 100%;
  left: 0;
}
.header__title {
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 20px;
  line-height: 110%;
  color: #fff;
}
.header__descr {
  font-size: 14px;
  line-height: 110%;
}
.header__bottom {
  background: var(--accent);
  color: #9ED8FE;
}
.header__bottom-inner {
  padding-top: 14px;
  padding-bottom: 16px;
}
.header__bottom p {
  margin-bottom: 4px;
}
.header__bottom-text {
  color: #fff;
}
.header__bottom-help {
  text-decoration: underline;
}
.header__bottom-help:hover, .header__bottom-help:focus {
  text-decoration: none;
}

@media all and (min-width: 360px) {
  .header__top-inner {
    padding: 13px 14px;
  }
}
@media all and (min-width: 480px) {
  .header__top-inner {
    padding: 13px 18px;
  }
  .header__burger {
    margin-right: 26px;
  }
  .header__city-current {
    font-size: 12px;
  }
  .header__contact-tel {
    font-size: 14px;
  }
  .header__logo {
    width: 170px;
    min-width: 170px;
  }
  .header__main-inner {
    min-height: 284px;
    padding-top: 36px;
    padding-bottom: 38px;
  }
  .header__menu {
    margin-bottom: 53px;
  }
  .header__title {
    margin-bottom: 15px;
    font-size: 30px;
    font-weight: 800;
  }
  .header__descr {
    font-size: 16px;
  }
  .header__bottom-inner {
    padding-bottom: 12px;
  }
  .header__bottom-info {
    margin-bottom: 10px;
  }
}
@media all and (min-width: 768px) {
  .header__top-inner {
    padding: 13px 30px;
  }
  .header__main-inner {
    padding: 36px 30px;
  }
  .header__title {
    margin-bottom: 32px;
  }
  .header__bottom {
    font-size: 12px;
  }
  .header__bottom-inner {
    padding: 10px 30px 9px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header__bottom-info {
    margin-bottom: 0;
  }
}
@media all and (min-width: 1280px) {
  .header__burger {
    display: none;
  }
  .header__city {
    margin-right: 34px;
  }
  .header__time {
    display: flex;
    align-items: center;
    color: #949CAC;
    font-size: 12px;
    line-height: 100%;
  }
  .header__time p {
    margin-right: 12px;
  }
  .header__time p:last-child {
    margin-right: 0;
  }
  .header__main-inner {
    min-height: 350px;
  }
  .header__menu {
    margin-bottom: 70px;
    align-items: flex-end;
    justify-content: flex-start;
  }
  .header__logo {
    margin-right: 56px;
  }
  .header__write {
    margin-left: auto;
  }
  .header__nav {
    width: 100%;
    max-width: 705px;
    display: block;
  }
  .header__nav-list {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  .header__nav-item {
    margin-right: 40px;
  }
  .header__nav-item:last-child {
    margin-right: 0;
  }
  .header__nav-link {
    font-size: 15px;
    line-height: 110%;
    font-weight: bold;
  }
  .header__bottom {
    font-size: 14px;
  }
  .header__bottom-inner {
    padding: 18px 30px 17px;
  }
  .header__bottom-info {
    display: flex;
    flex-wrap: wrap;
  }
  .header__bottom-info p {
    margin-right: 12px;
  }
  .header__bottom-info p:last-child {
    margin-right: 0;
  }
  .header__bottom-help {
    min-width: 150px;
  }
}
@media all and (min-width: 1600px) {
  .header__main-inner {
    min-height: 520px;
  }
  .header__menu {
    margin-bottom: 110px;
  }
  .header__logo {
    margin-right: 84px;
  }
  .header__nav-item {
    margin-right: 54px;
  }
  .header__title {
    font-size: 50px;
  }
  .header__bottom-info p {
    margin-right: 16px;
  }
}
.service {
  padding: 24px 0 34px;
  background: #fff;
  overflow-x: hidden;
}
.service__inner {
  padding: 0;
}
.service__header {
