  height: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: red;
  border-radius: 50%;
  transition: height 0.2s ease;
}
.footer__soc-link:hover::after, .footer__soc-link:focus::after {
  height: 80%;
}
.footer__soc-link--youtube::after {
  background: #ff0000;
}
.footer__soc-link--vk::after {
  background: #0077ff;
}
.footer__soc-link--ok::after {
  background: #ec820a;
}
.footer__soc-link--twitter::after {
  background: #40abe2;
}
.footer__soc-link svg {
  display: block;
  width: 33px;
  height: 33px;
  position: relative;
  z-index: 10;
}
.footer__copy {
  padding: 23px 0;
  background: var(--accent);
  color: #fff;
  font-weight: 600;
  font-size: 10px;
  line-height: 130%;
}

